<template>
  <div id="wrap">
    <section-title sec-title="配送"></section-title>
    <tabs :paths="paths"></tabs>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import sectionTitle from '../others/section-title'
  import tabs from '../others/tabs'
  export default {
    components: {
      sectionTitle,
      tabs
    },
    name: 'distribution',
    data () {
      return {
        paths:[
          {
            title: '全部',
            path: '/distribution/all'
          },
          {
            title: '未送达',
            path: '/distribution/undelivered'
          },
          {
            title: '已送达',
            path:'/distribution/delivered'
          }
        ]
      }
    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #wrap{
    position: relative;
    background-color: #fff;
    width: 1030px;
    height: 968px;
    margin: 22px 0 0 26px;
  }
</style>



